/* mobile full height fix */
:root
  --app-height: 100%
*
  box-sizing: border-box
  outline: none
  &::before, &::after
    box-sizing: border-box
body,
h1, h2, h3, h4, h5, h6, p,
ul[class], ol[class], li,
figure, figcaption, blockquote,
dl, dd
  margin: 0
ul[class], ol[class]
  padding: 0
  list-style-type: none
a
  &:not([class])
    text-decoration-skip-ink: auto
    color: inherit
  &[class]
    text-decoration: none
input, button, textarea, select
  font: inherit
button, label
  cursor: pointer
html
  margin-right: 0 !important
body
  display: flex
  flex-direction: column
  min-height: 100vh
  min-width: 320px
  position: relative
  font-family: $default-font
  font-weight: 400
  font-size: $default-fz
  line-height: $default-lh
  color: $txt-color
  background-color: $bg-color
  scroll-behavior: smooth
  text-rendering: optimizeSpeed
  overflow-x: hidden
  @include scrollbars($scrl-bar_width, $scrl-bar_track, $scrl-bar_thumb, $scrl-bar_thumbh, 0)
  @media (max-width: 1199px)
    font-size: 16px
    line-height: 22px
  &.overflow
    overflow: hidden
    padding-right: var(--scroll-width)
    .header
      padding-right: var(--scroll-width)
    &.menu-show
      &:before
        content: ''
        display: block
        width: 100%
        height: 100%
        position: fixed
        top: 0
        left: 0
        background-color: rgba(0,0,0, .5)
        z-index: 2
      .header__content
        &:before
          content: ''
          display: block
          width: 100%
          height: 100%
          position: absolute
          top: 0
          left: 0
          background-color: rgba(0,0,0, .5)
          z-index: 2
.container
  width: 100%
  margin: 0 auto
  max-width: $container-width
  padding: $container-padding


