@import "settings"
@import "_mixins/_mixins"
@import "_fonts/fonts_list"
@import "_fonts/fontello"                                                                                               // if you use fontello fonts
@import "_components/reset"
@import "_components/header"

/* main page */
main
  padding-top: 130px
  @media (max-width: 959px)
    padding-top: 132px
.main-page
  &__title
    font-family: Manrope, sans-serif
    font-weight: 700
    font-size: 59px
    line-height: 125%
    letter-spacing: .01em
    margin-top: 75px
    @media (max-width: 1335px)
      margin-top: 35px
    @media (max-width: 1199px)
      font-size: 50px
    @media (max-width: 1023px)
      font-size: 45px
    @media (max-width: 959px)
      font-size: 37px
    @media (max-width: 575px)
      font-size: 28px
    @media (max-width: 419px)
      font-size: 32px
    @media (max-width: 359px)
      font-size: 28px
    span
      display: inline-block
      color: $accent-color
      @media (max-width: 419px)
        font-size: 25px
        font-weight: 800
        letter-spacing: 0
      @media (max-width: 359px)
        font-size: 24px
  &__subtitle
    margin-top: 40px
    font-weight: 600
    font-size: 24px
    line-height: 33px
    @media (max-width: 1199px)
      margin-top: 25px
    @media (max-width: 1023px)
      margin-top: 15px
      font-size: 22px
      line-height: 30px
    @media (max-width: 959px)
      font-size: 20px
    @media (max-width: 639px)
      font-size: 18px
    @media (max-width: 575px)
      line-height: 24px
  &__image-wrapper
    background-repeat: no-repeat
    background-position: center
    background-size: cover
    width: calc(100% + (100vw - 100%))
    margin: 75px auto 0
    min-height: 460px
    @media (max-width: 1023px)
      margin: 65px auto 0
    @media (max-width: 767px)
      min-height: 375px
  .link-wrapper
    display: inline-block
    position: relative
    padding: 41px 90px
    margin: 0 -90px
    top: -72px
    @media (max-width: 359px)
      padding: 20px 0
      margin: 0 auto
      top: -45px
      left: 0
      max-width: 100%
      overflow: hidden
    &:after
      content: ''
      display: block
      width: 100%
      height: 100%
      background-image: url(../images/common/svg/main_link_bg.svg)
      background-repeat: no-repeat
      background-position: center
      background-size: contain
      position: absolute
      top: 0
      left: 0
      @media (max-width: 359px)
        display: none
  .sample-link
    display: block
    width: 360px
    background-color: $accent-color
    border-radius: 100px
    color: #fff
    font-weight: 700
    font-size: 21px
    line-height: 21px
    text-align: center
    padding: 20px
    position: relative
    z-index: 1
    transition: background-color .3s
    @media (max-width: 767px)
      font-size: 18px
      max-width: 320px
      padding: 15px
    @media (max-width: 575px)
      max-width: 280px
    @media (max-width: 359px)
      max-width: 100%
    &:hover
      background-color: $accent-hover-color

.main-how
  margin-top: 120px
  @media (max-width: 1335px)
    margin-top: 75px
  @media (max-width: 1199px)
    margin-top: 45px
  &__title
    margin-top: 22px
    @media (max-width: 959px)
      margin-top: 10px
  &__content
    background-color: #fff
    border: 1px solid #E8E8E8
    border-radius: 32px
    display: flex
    align-items: stretch
    justify-content: flex-start
    flex-wrap: wrap
    padding: 30px 20px
    @media (max-width: 767px)
      padding: 20px
      border-radius: 24px
    &__left-side
      flex: 0 0 calc(50% - 19px)
      max-width: calc(50% - 19px)
      @media (max-width: 959px)
        flex: 0 0 calc(50% - 10px)
        max-width: calc(50% - 10px)
      @media (max-width: 767px)
        flex: 0 0 100%
        max-width: 100%
        order: 1
      .how-video-link
        display: block
        position: relative
        &:after
          content: '\e808'
          font-family: Fontello
          font-style: normal
          font-weight: normal
          speak: never
          display: block
          margin: 0
          text-decoration: inherit
          text-align: center
          font-variant: normal
          text-transform: none
          position: absolute
          top: calc(50% - 40px)
          left: calc(50% - 40px)
          color: rgba(230, 233, 242, 0.8)
          font-size: 80px
          line-height: 80px
          transition: transform .3s
          @media (max-width: 1199px)
            font-size: 60px
            line-height: 60px
            top: calc(50% - 30px)
            left: calc(50% - 30px)
          @media (max-width: 575px)
            font-size: 48px
            line-height: 48px
            top: calc(50% - 24px)
            left: calc(50% - 24px)
        .image
          display: block
          width: auto
          max-width: 100%
          margin: 0
          border-radius: 24px
          @media (max-width: 767px)
            width: 100%
        &:hover
          &:after
            transform: scale(1.1)
    &__right-side
      flex: 0 0 calc(50% - 19px)
      max-width: calc(50% - 19px)
      margin-left: 38px
      @media (max-width: 959px)
        flex: 0 0 calc(50% - 10px)
        max-width: calc(50% - 10px)
        margin-left: 20px
      @media (max-width: 767px)
        flex: 0 0 100%
        max-width: 100%
        margin: 0 0 20px
      .tag
        display: inline-block
        font-weight: 400
        font-size: 15px
        line-height: 120%
        color: $txt-gray-color
        text-transform: uppercase
        padding: 7px 25px
        border: 1px solid $border-gray-color
        border-radius: 8px
        @media (max-width: 959px)
          font-size: 13px
          padding: 7px 15px
        @media (max-width: 419px)
          display: block
          text-align: center
      .descr-block
        margin-top: 30px
        letter-spacing: .015em
        @media (max-width: 959px)
          margin-top: 15px



