[class^="icon-"]:before,
[class*=" icon-"]:before
  font-family: Fontello
  font-style: normal
  font-weight: normal
  speak: never
  display: block
  margin: 0
  text-decoration: inherit
  text-align: center
  font-variant: normal
  text-transform: none

/* your fontello items here */

.icon-check:before
  content: '\e800'
.icon-close:before
  content: '\e801'
.icon-download:before
  content: '\e802'
.icon-file:before
  content: '\e803'
.icon-link_arrow:before
  content: '\e804'
.icon-minus_circle:before
  content: '\e805'
.icon-person:before
  content: '\e806'
.icon-phone:before
  content: '\e807'
.icon-play:before
  content: '\e808'
.icon-plus_circle:before
  content: '\e809'
.icon-select_arrow:before
  content: '\e80a'
.icon-slider_arrow:before
  content: '\e80b'
.icon-wa:before
  content: '\e80c'
.icon-time:before
  content: '\e80d'

.custom-icon
  display: inline-block
  width: 24px
  height: 24px
  &.icon-personal:before
    content: url("../images/common/svg/icons/personal.svg")
  &.icon-phone-mini
