/* mobile full height fix */
:root {
  --app-height: 100%;
}

* {
  box-sizing: border-box;
  outline: none;
}
*::before, *::after {
  box-sizing: border-box;
}

body,
h1, h2, h3, h4, h5, h6, p,
ul[class], ol[class], li,
figure, figcaption, blockquote,
dl, dd {
  margin: 0;
}

ul[class], ol[class] {
  padding: 0;
  list-style-type: none;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
  color: inherit;
}
a[class] {
  text-decoration: none;
}

input, button, textarea, select {
  font: inherit;
}

button, label {
  cursor: pointer;
}

html {
  margin-right: 0 !important;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-width: 320px;
  position: relative;
  font-family: OpenSans, sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 25px;
  color: #2D373D;
  background-color: #F0F3F8;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  overflow-x: hidden;
}
body::-webkit-scrollbar {
  width: 6px;
  height: 6px;
  border-radius: 0;
}
body::-webkit-scrollbar-thumb {
  background: #116893;
  border-radius: 0;
  transition: background-color 0.3s;
}
body::-webkit-scrollbar-thumb:hover {
  background: #116893;
}
body::-webkit-scrollbar-track {
  background: #fff;
  border-radius: 0;
}
body {
  scrollbar-face-color: #116893;
  scrollbar-track-color: #fff;
}
@media (max-width: 1199px) {
  body {
    font-size: 16px;
    line-height: 22px;
  }
}
body.overflow {
  overflow: hidden;
  padding-right: var(--scroll-width);
}
body.overflow .header {
  padding-right: var(--scroll-width);
}
body.overflow.menu-show:before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 2;
}
body.overflow.menu-show .header__content:before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 2;
}

.container {
  width: 100%;
  margin: 0 auto;
  max-width: 1190px;
  padding: 0 15px;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 20;
}
.header__content__top {
  background-color: #F0F3F8;
  padding: 8px 0 5px;
}
@media (max-width: 1023px) {
