.header
  position: fixed
  top: 0
  left: 0
  width: 100%
  z-index: 20
  &__content
    &__top
      background-color: $bg-color
      padding: 8px 0 5px
      @media (max-width: 1023px)
        padding: 8px 0
      .container
        display: flex
        align-items: center
        justify-content: flex-start
        @media (max-width: 1023px)
          padding: 0 30px
        @media (max-width: 767px)
          padding: 0 15px
        .work
          display: inline-block
          color: $txt-gray-color
          font-weight: 500
          font-size: 16px
          line-height: 22px
          margin-right: 40px
          @media (max-width: 1199px)
            font-size: 14px
            line-height: 20px
          @media (max-width: 959px)
            font-size: 15px
          @media (max-width: 575px)
            max-width: 230px
            font-size: 14px
            margin-right: auto
        .phone-link
          display: inline-block
          padding-left: 20px
          color: $txt-color
          font-family: Manrope, sans-serif
          font-weight: 700
          font-size: 20px
          line-height: 26px
          letter-spacing: .03em
          position: relative
          transition: color .3s
          @media (max-width: 1199px)
            font-size: 18px
          @media (max-width: 1023px)
            display: none
          &:hover
            color: $accent-color
          &:before
            content: ''
            display: block
            width: 8px
            height: 8px
            background-color: $txt-green-color
            border-radius: 50%
            position: absolute
            top: calc(50% - 4px)
            left: 0
        .wa-link
          display: none
          @media (max-width: 1023px)
            display: flex
            align-items: center
            justify-content: center
            width: 50px
            height: 50px
            border: 1px solid $border-gray-color
            border-radius: 50%
            color: #39AE41
            font-size: 24px
            line-height: 24px
            margin: 0 0 0 auto
        .backcall-link
          display: inline-block
          margin: 0 0 0 auto
          padding: 10px
          color: $txt-color
          font-weight: 500
          font-size: 16px
          line-height: 22px
          transition: color .3s
          @media (max-width: 1023px)
            margin: 0 0 0 20px
          @media (max-width: 959px)
            display: none
          &:hover
            color: $accent-color
        .sample-link
          display: inline-flex
          align-items: center
          justify-content: flex-start
          padding: 10px 20px
          background-color: $accent-color
          color: #fff
          text-transform: uppercase
          font-weight: 600
          font-size: 15px
          line-height: 15px
          margin-left: 20px
          border-radius: 8px
          transition: background-color .3s
          @media (max-width: 959px)
            display: none
          .icon
            font-size: 12px
            display: inline-block
            margin-left: 10px
            transition: transform .3s
          &:hover
            background-color: $accent-hover-color
            .icon
              transform: translateX(3px)
    &__bottom
      background-color: #fff
      padding: 8px 0 10px
      .container
        display: flex
        align-items: center
        justify-content: flex-start
        @media (max-width: 1023px)
          padding: 0 30px
        @media (max-width: 767px)
          padding: 0 15px
      .logo-link
        display: inline-block
        .logo-image
          display: block
          width: auto
          max-width: 100%
          margin: 0
          position: relative
          left: 3px
          @media (max-width: 1199px)
            left: 0
            max-width: 120px

      .main-menu-wrapper
        display: flex
        align-items: center
        justify-content: flex-start
        flex: 1 1 auto
        width: auto
        @media (max-width: 1023px)
          display: flex
          flex-direction: column
          align-items: flex-start
          justify-content: flex-start
          position: absolute
          top: 0
          right: 0
          margin: 0
          transform: translateX(100vw)
          transition: transform .3s
          background-color: #fff
          height: var(--app-height)
          width: 260px
          z-index: 10
          padding-top: 60px
          &.show
            transform: translateX(0)
        .mobile-wrapper
          display: flex
          align-items: center
          justify-content: flex-start
          flex: 1 1 auto
          width: auto
          @media (max-width: 1023px)
            flex-direction: column
            align-items: flex-start
            justify-content: flex-start
            width: 100%
            flex: 0 0 auto
            order: 1
            overflow-y: scroll
            padding-bottom: 20px
            max-height: calc(var(--app-height) - 66px - 80px)
        .logo-link
          display: none
          @media (max-width: 1023px)
            display: inline-block
            margin: 15px 20px
            order: 1
          .logo-image
            display: block
            width: auto
            max-width: 100%
            margin: 0
            width: 120px
        .sample-link
          display: none
          @media (max-width: 1023px)
            order: 4
            display: flex
            align-items: center
            justify-content: center
            padding: 12px 20px
            background-color: $accent-color
            color: #fff
            text-transform: uppercase
            font-weight: 600
            font-size: 15px
            line-height: 18px
            margin: 12px 20px 0
            border-radius: 8px
            transition: background-color .3s
            width: 100%
            max-width: calc(100% - 40px)
            .icon
              font-size: 12px
              display: inline-block
              margin-left: 10px
              transition: transform .3s
            &:hover
              background-color: $accent-hover-color
              .icon
                transform: translateX(3px)
        .work
          display: none
          @media (max-width: 1023px)
            display: block
            order: 5
            color: $txt-gray-color
            font-weight: 500
            font-size: 15px
            line-height: 20px
            padding: 0 20px
            margin-top: 20px
        .phone-link
          display: none
          @media (max-width: 1023px)
            order: 6
            display: inline-block
            padding-left: 20px
            margin: 20px 20px 0
            color: $txt-color
            font-family: Manrope, sans-serif
            font-weight: 700
            font-size: 18px
            line-height: 24px
            letter-spacing: .02em
            position: relative
            transition: color .3s
            &:hover
              color: $accent-color
            &:before
              content: ''
              display: block
              width: 8px
              height: 8px
              background-color: $txt-green-color
              border-radius: 50%
              position: absolute
              top: calc(50% - 4px)
              left: 0
      .main-menu
        margin-left: 55px
        @media (max-width: 1023px)
          margin: 5px 0 0
          padding: 10px 0
          order: 3
          border-top: 1px solid $border-gray-color
          width: 100%
        &__list
          display: flex
          align-items: center
          justify-content: flex-start
          @media (max-width: 1023px)
            flex-direction: column
            align-items: flex-start
        &__item
          flex: 0 0 auto
          width: auto
          margin: 0 28px
          @media (max-width: 1023px)
            margin: 20px 0 0
            padding: 0 20px
            width: 100%
            &:nth-of-type(1)
              margin-top: 12px
        &__link
          display: inline-block
          text-transform: uppercase
          color: $txt-gray-color
          font-weight: 500
          font-size: 15px
          line-height: 15px
          padding: 6px 0
          position: relative
          @media (max-width: 1023px)
            padding: 5px 0
            display: flex
            align-items: center
            justify-content: flex-start
            position: relative
            &:after
              font-family: Fontello
              font-style: normal
              font-weight: normal
              speak: never
              display: flex
              align-items: center
              justify-content: center
              text-decoration: inherit
              text-align: center
              font-variant: normal
              text-transform: none
              content: '\e804'
              margin: 0 0 0 auto
              color: $txt-gray-color
              font-size: 12px
              line-height: 12px
          &:before
            content: ''
            display: block
            width: 0
            height: 1px
            background-color: $accent-color
            position: absolute
            bottom: -2px
            left: 0
            transition: width .3s
            @media (max-width: 1023px)
              display: none
          &:hover
            color: $accent-color
            &:after
              @media (max-width: 1023px)
                color: $accent-color
            &:before
              width: 100%
      .personal-block
        display: flex
        align-items: center
        justify-content: flex-start
        margin: 0 0 0 auto
        position: relative
        @media (max-width: 1023px)
          order: 2
          width: 100%
          margin: 5px 0 0 0
          padding: 8px 20px
          flex-direction: column
          align-items: flex-start
        .personal-link
          display: inline-flex
          align-items: center
          justify-content: flex-start
          text-transform: uppercase
          color: $accent-color
          font-weight: 500
          font-size: 15px
          line-height: 15px
          .custom-icon
            margin-right: 13px
        .personal-menu
          position: absolute
          width: 290px
          top: calc(50% + 38px)
          right: 0
          background-color: #fff
          box-shadow: 0px 3px 10px rgba(17, 104, 147, 0.2)
          border-radius: 24px
          padding: 20px
          @media (max-width: 1023px)
            position: relative
            width: 100%
            top: 0
            box-shadow: none
            padding: 0
            margin-top: 13px
            border-radius: 0
          .btn-border-style
            display: block
            width: 250px
            font-weight: 500
            font-size: 18px
            line-height: 133%
            padding: 13px
            @media (max-width: 1023px)
              width: 100%
              font-size: 15px
              line-height: 20px
              padding: 9px
              border-radius: 8px
            & + .btn-border-style
              margin-top: 20px
              @media (max-width: 1023px)
                margin-top: 15px
          .user-name
            display: inline-flex
            align-items: flex-start
            justify-content: flex-start
            padding-left: 35px
            position: relative
            font-weight: 600
            font-size: 16px
            line-height: 22px
            margin-bottom: 12px
            @media (max-width: 1023px)
              margin-top: 5px
            &:before
              font-family: Fontello
              font-style: normal
              font-weight: normal
              speak: never
              display: block
              margin: 0
              text-decoration: inherit
              text-align: center
              font-variant: normal
              text-transform: none
              content: '\e806'
              color: $accent-color
              font-size: 24px
              line-height: 24px
              position: absolute
              top: 0
              left: 0
          .user-menu
            position: relative
            padding: 12px 0
            &__item
              display: block
              text-align: center
              margin-top: 10px
              &:nth-of-type(1)
                margin-top: 0
            &__link
              display: inline-block
              font-weight: 500
              font-size: 15px
              line-height: 18px
              padding: 5px
              color: $txt-gray-color
              transition: color .3s
              position: relative
              &:before
                content: ''
                display: block
                width: 0
                height: 1px
                background-color: $accent-color
                position: absolute
                bottom: 0
                left: 0
                transition: width .3s
              &:hover
                color: $accent-color
                &:before
                  width: 100%
            &:before
              content: ''
              display: block
              width: calc(100% + 40px)
              height: 1px
              position: absolute
              top: 0
              left: -20px
              background-color: $border-gray-color
          .logout-link
            display: flex
            align-items: center
            justify-content: center
            color: $accent-color
            transition: color .3s
            font-weight: 500
            font-size: 16px
            line-height: 22px
            margin: 15px auto 0
            &:hover
              color: $accent-hover-color
            .custom-icon
              margin-right: 12px
      @import "hamburger"
  &.scroll
    .header__content__bottom
      .main-menu-wrapper
        padding-top: 0
        .mobile-wrapper
          max-height: calc(var(--app-height) - 66px - 12px)
        .logo-link
          margin: 8px 20px 10px




